import { Component, OnInit, Inject } from '@angular/core';
import { MatSnackBar} from '@angular/material';
import { Router } from '@angular/router';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';

import { DetailsmodalComponent } from '../detailsmodal/detailsmodal.component';
import  { JobDetails } from '../jobdetails';
import { JobService } from '../job.service';
import { GeneralService } from '../../../general.service';

@Component({
  selector: 'app-watchlist',
  templateUrl: './watchlist.component.html',
  styleUrls: ['./watchlist.component.css']
})
export class WatchlistComponent implements OnInit {
  jobDetailsList: Array<JobDetails>;
  constructor(private jobService: JobService,private generalService: GeneralService,
    public snackbar: MatSnackBar,private routes: Router,public dialog: MatDialog) { 
  }
  getWatchlistedJobs(){
    this.jobService.getWatchlistedjobs().subscribe(jobDetails => {
      this.jobDetailsList =  jobDetails;
      console.log(this.jobDetailsList);      
    });
  }

  ngOnInit() {
    this.getWatchlistedJobs();
  }

  deleteFromWatchList(jobId: number){
    this.jobService.deleteFromMyWatchList(jobId).subscribe(jobDetails => {
      this.snackbar.open("Job Deleted From watchList successfully" ,"", {
        duration: 2000,
      });
      //window.location.reload();
      this.ngOnInit();
    })
  }

  openDialogForUpdate(jobDetails: JobDetails): void {
    const dialogRef = this.dialog.open(DetailsmodalComponent, {
      width: '350px',
      data: { obj: jobDetails, actionType:'update'}
    });

    dialogRef.afterClosed().subscribe(res => {
      
    });
  }

}
