import { Component, ViewEncapsulation, ElementRef, Input, OnInit, OnDestroy } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';


import { DetailsmodalComponent } from '../detailsmodal/detailsmodal.component';
import { JobService } from '../job.service';
import { GeneralService } from '../../../general.service';
import { Router } from '@angular/router';
import  { Job } from '../job';
import  { JobDetails } from '../jobdetails';


@Component({
  selector: 'app-tabularview',
  templateUrl: './tabularview.component.html',
  styleUrls: ['./tabularview.component.css']
})
export class TabularviewComponent implements OnInit {
  jobs: Array<Job>;
  jobDetails: JobDetails;
  config: any;
  constructor(private jobService: JobService,private generalService: GeneralService,
    private routes: Router,public dialog: MatDialog) {
    this.jobDetails = new JobDetails();
    console.log(this.generalService.searchParam);
    this.populateJobList();
  }
 
  ngOnInit() {
    
  }

  populateJobList(){
    this.jobService.searchJobs(this.generalService.searchParam).subscribe(jobs => {
      this.jobs= jobs;
      console.log(this.jobs);
      console.log("jobs array length"+this.jobs.length);
      this.config = {
        itemsPerPage: 5,
        currentPage: 1,
        totalItems: this.jobs.length
      };
    });
  }

  pageChanged(event){
    this.config.currentPage = event;
  }

  getJobDetails(job: Job){
    console.log("job company ==="+job.company);
    this.jobDetails.id=job.id;
    this.jobDetails.name=job.name;
    this.jobDetails.company=job.company.name;
    this.jobDetails.locations= '';
    this.jobDetails.categories= '';
    this.jobDetails.levels= '';
    //setting  the Locations
    let locations = job.locations;
    for(var i=0;i<locations.length; i++){
      if(i>0){
        this.jobDetails.locations= this.jobDetails.locations+",";
      }
      this.jobDetails.locations = this.jobDetails.locations + locations[i].name;
    }
    //setting  the Categories
    let categories = job.categories;
    for(var i=0;i<categories.length; i++){
      if(i>0){
        this.jobDetails.categories= this.jobDetails.categories+",";
      }
      this.jobDetails.categories = this.jobDetails.categories + categories[i].name;
    }
    //setting  the levels
    let levels = job.levels;
    for(var i=0;i<levels.length; i++){
      if(i>0){
        this.jobDetails.levels= this.jobDetails.levels+",";
      }
      this.jobDetails.levels = this.jobDetails.levels + levels[i].name;
    } 

    this.openJobDetailsDialog(this.jobDetails);

  }
  openJobDetailsDialog(jobDetails): void {
    const dialogRef = this.dialog.open(DetailsmodalComponent, {
      width: '350px',
      data: { obj: jobDetails, actionType:'add'}
    });

    dialogRef.afterClosed().subscribe(res => {
      
    });
  }
}