import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import  { Job } from '../job';
import  { Company } from '../company';
import {FormControl} from '@angular/forms';
import {Observable} from 'rxjs';
import {map, startWith} from 'rxjs/operators';

import { JobService } from '../job.service';
import { GeneralService } from '../../../general.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.css']
})
export class SearchComponent implements OnInit {
  companies: Array<Company>;
  

  @Output() srchKeyEvent = new EventEmitter<any>();

  myControl = new FormControl();
  filteredOptions: Observable<Company[]>;

  constructor(private jobService: JobService,private generalService: GeneralService,private routes: Router) { 
    this.companies=[];
  }

  ngOnInit() {
    this.loadCompnies();
  }

  displayFn(company?: Company): string | undefined {
    return company ? company.name : undefined;
  }

  private _filter(name: string): Company[] {
    const filterValue = name.toLowerCase();

    return this.companies.filter(option => option.name.toLowerCase().indexOf(filterValue) === 0);
  }

  loadCompnies(){
    this.jobService.loadCompnies().subscribe(companies => {
      this.companies.push(...companies);
      console.log(this.companies);
      this.loadFilteroption();
    }) 
  }
  
  loadFilteroption(){
    this.filteredOptions = this.myControl.valueChanges
      .pipe(
        startWith(''),
        map(value => typeof value === 'string' ? value : value.name),
        map(name => name ? this._filter(name) : this.companies.slice())
      );
  }

  onEnterSearchBox(searchKey){
    console.log("SearchComponent"+searchKey);
    this.generalService.setSearchParamValue(searchKey);
    this.routes.navigate(['/job/tabularview']);
  };
}