import { Component, OnInit, Inject } from '@angular/core';
import { MatSnackBar, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Router } from '@angular/router';

import  { JobDetails } from '../jobdetails';
import { JobService } from '../job.service';
@Component({
  selector: 'app-detailsmodal',
  templateUrl: './detailsmodal.component.html',
  styleUrls: ['./detailsmodal.component.css']
})
export class DetailsmodalComponent implements OnInit {
  jobDetails: JobDetails;  
  actionType: String;
  comments: String;
  constructor(private jobService: JobService,public dialogRef: MatDialogRef<DetailsmodalComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any, public snackbar: MatSnackBar, private router: Router ) {
      this.jobDetails=data.obj;
      this.actionType=data.actionType;
      this.comments=data.obj.comments;
    
  }
  ngOnInit(): void {}

  addToWatchList(jobDetails : JobDetails){
    this.dialogRef.close();
    this.jobService.addJobToWatchlist(jobDetails).subscribe(
      jobDetails => {
        this.snackbar.open("Job added to watchList successfully" ,"", {
          duration: 2000,
        });
      },
      error => {
        if(!!error && !!error.error){
          this.snackbar.open(error.error ,"", {
            duration: 2000,
          });
        }	
      }
    )
  }

  updateJobComments(){
    this.jobDetails.comments=this.comments;
    this.dialogRef.close();
    this.jobService.updateJobComments(this.jobDetails).subscribe(
      job => {
        this.snackbar.open("Job's comment updated successfully" ,"", {
          duration: 2000,
        });
        this.router.navigate(['/job/watchlist']);
      },
      error => {
        if(!!error && !!error.error){
          this.snackbar.open(error.error ,"", {
            duration: 2000,
          });
        }	
      }
    )
  }

  closeDialog(){
    this.dialogRef.close();
  }
}
