import { OnInit, Component } from '@angular/core';
import { MatSnackBar } from '@angular/material';

import { AuthenticationService } from './../authentication.service';

import { User } from './../User';
import { Router } from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  newUser: User;

	constructor(
		private authenticationService: AuthenticationService,
		private router: Router,
		public snackbar: MatSnackBar
	) {
		this.newUser = new User();
	}

	ngOnInit() {
	}

	registerNewUser() {
		if(this.newUser.userId == null || this.newUser.userId.trim().length ==0){
			this.snackbar.open("User Id is required" ,"", {
        		duration: 1000,
      		});
		}	
		else if(this.newUser.password == null || this.newUser.password.trim().length ==0){
			this.snackbar.open("Password is required" ,"", {
        		duration: 1000,
      		});
		}	
		else{
			this.authenticationService.registerUser(this.newUser).subscribe(
				data => {
					this.snackbar.open("User registered successfully" ,"", {
						duration: 1000,
					});
					this.router.navigate(['/login']);
				},
				error => {
					if(!!error && !!error.error){
						this.snackbar.open(error.error ,"", {
							duration: 1000,
						});
					}	
				}
			);
		}
		
	}
}
