import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { Job } from './job';
import { JobDetails } from './jobdetails';
import { Company } from './company';
import { Observable } from 'rxjs';
import { retry } from 'rxjs/operators';

@Injectable()
export class JobService {
  
  apikey: string;
  jobListApiEndpoint: string;
  companyEndpoint: string;
  watchlistEndpoint: string;
  jobDetailsApiEndpoint: string;
  job: Job;

  constructor(private http: HttpClient) { 
    this.companyEndpoint='https://www.themuse.com/api/public/companies?page=1';
    this.jobListApiEndpoint='https://www.themuse.com/api/public/jobs?page=1';
    this.jobDetailsApiEndpoint ='https://www.themuse.com/api/public/jobs/';
    this.watchlistEndpoint= 'http://ec2-54-190-144-141.us-west-2.compute.amazonaws.com:8082/musejob/api/job/';
  }

  loadCompnies() : Observable<Company[]>{
    const url=`${this.companyEndpoint}`;
    return this.http.get(url).pipe(retry(1),
    map(this.pickCompanyResults));
 
  }
  
  pickCompanyResults(response){
    return response['results']; 
  }

  searchJobs(searchKey : string) : Observable<Job[]>{
    var url=`${this.jobListApiEndpoint}`;
    if(searchKey.length>0){
      url=`${this.jobListApiEndpoint}&company=${searchKey}`;
    } 
    return this.http.get(url).pipe(retry(1),
    map(this.pickJobResults));
  }

  pickJobResults(response){
    return response['results']; 
  }

  getJobDetails(jobId : number) : Observable<Job[]>{
    var url=`${this.jobDetailsApiEndpoint}`;
    if(jobId>0){
      url=`${this.jobDetailsApiEndpoint}${jobId}`;
    } 
    console.log(url);
    console.log("service response"+this.http.get(url).pipe(retry(1),
    map(this.pickJobDetailsResults)));
    return this.http.get(url).pipe(retry(1),
    map(this.pickJobDetailsResults));

  }

  pickJobDetailsResults(response){
    return response['results'];
  }

  addJobToWatchlist(jobDetails : JobDetails){
    const url = this.watchlistEndpoint + "addJob";
    return this.http.post(url, jobDetails, { responseType: 'text' });
  }

  getWatchlistedjobs(): Observable<JobDetails[]>{
    const url = this.watchlistEndpoint + "getJobList";
    return this.http.get<Array<JobDetails>>(url);
  }  
    
  deleteFromMyWatchList(jobId : number){
   const url=`${this.watchlistEndpoint}+${jobId}`;
   return this.http.delete(url,{responseType : 'text'});
  }
  
  updateJobComments(jobDetails : JobDetails){
    const url=`${this.watchlistEndpoint}+${jobDetails.id}`;
    return this.http.put(url,jobDetails);
  }
}